import React from 'react'

import ReactSVG from 'react-svg'
import watchedIcon from '../../static/svg/checked.svg'

import {store} from "../../utils/store";
import {ActionTypes} from "../../utils/constants";
import {connect} from "react-redux";


const AddToWatchedBtn_ = (props) => {

    const data = {...props.data, mediaType: props.mediaType}

    const isActive = (data) => {
        return props.watched.some((item) => {
            return item.id === data.id
        })
    }
    const active = isActive(data)

    const addToWatched = (data) => {
        if (active) {
            store.dispatch({
                type: ActionTypes.REMOVE_FROM_WATCHED,
                item: data
            })

            return;
        }
        store.dispatch({type: ActionTypes.ADD_TO_WATCHED, item: data})
    }

    return (
        <div className={(active) ? "add-to-watched add-to-list_active" : 'add-to-watched'} onClick={()=>addToWatched(data)}>
            <ReactSVG src={watchedIcon}
                      svgClassName='action-icon add-to-watched__icon'/>
        </div>
    )
}

const mapStateToProps = (state)=>{
    return state.lists
}

export const AddToWatchedBtn = connect(mapStateToProps)(AddToWatchedBtn_)