import React from 'react'

import {Header} from "../../components/header"
import {Container} from "../../components/container"

import closeIcon from '../../static/svg/close-cross.svg'
import {API_KEY} from "../../utils/constants";

import ReactStars from 'react-stars'
import ReactSVG from 'react-svg'

import {PlayTrailerBtn} from "../../components/buttons/play-trailer";
import {AddToFavoritiesBtn} from "../../components/buttons/add-to-favorities";
import {AddToWatchedBtn} from "../../components/buttons/add-to-watched";
import {AddToWishlistBtn} from "../../components/buttons/add-to-wishlist";
import {formMovieCardData} from "../../utils/functions";
import {MovieCard} from "../../components/movie-card";
import {Pagination} from "../../components/pagination";


import {Footer} from "../../components/footer"

const reducer = (prevState, newState) => {
    return {...prevState, ...newState}
}

export const Recommendations  = () => {
    const categories = [
        {
            name:  'Movies',
            mediaType: 'movie'
        },
        {
            name:  'TV shows',
            mediaType: 'tv'
        }]
    const initialState = {
        mediaType: 'movie',
        totalPages: undefined,
        addedValue:  undefined,
        page: 1,
        query: '',
        suggestions: [],
        recommendations: []

    }

    const [pageState, dispatch] = React.useReducer(
        reducer,
        initialState
    )

  const resetPage = () => {
        dispatch(initialState)
    }

    const deleteValue = (value) => {
        dispatch({addedValue: undefined})
    }
    const addValue = (value) => {
        dispatch({addedValue: value,
            query: '',
            suggestions: [],
            page: 1
        })
    }

    const switchMediaType = (mediaType) =>{
        resetPage()
        dispatch({mediaType})
    }

    const handleInput = (e) => {
        dispatch({query: e.target.value})
    }

    React.useEffect(()=>{

        if (pageState.query.length >= 3){

            const searchQuery = `https://api.themoviedb.org/3/search/`
            +`${pageState.mediaType}?`
            +`api_key=${API_KEY}&language=en-US&`
            +`query=${pageState.query}`

            fetch(searchQuery)
                .then((response) => {
                    if (response.ok){
                        return response.json()
                    }
                    throw new Error(`Cannot connect to the server to get items `)
                }).then((data)=> {
                dispatch({suggestions: data.results})
            })}


        if (pageState.addedValue){

            const recommendQuery = `https://api.themoviedb.org/3/${pageState.mediaType}/`
                +`${pageState.addedValue.id}/recommendations?`
                +`api_key=${API_KEY}&language=en-US&`
                +`page=${pageState.page}`


            fetch(recommendQuery)
                .then((response) => {
                    if (response.ok){
                        return response.json()
                    }
                    throw new Error(`Cannot connect to the server to get items `)
                }).then((data)=> {

                dispatch({recommendations: data.results , totalPages: data.total_pages})
            })
        }



    }, [pageState.mediaType,
        pageState.query,
        pageState.page,
        pageState.addedValue
    ])
    const handlePageClick = ({selected}) =>{
        dispatch({page: selected + 1})
    }

    return (
        <React.Fragment>
            <Header/>
            <Container>
                <div className="recommendations-page" style={ ( pageState.recommendations.length > 0) ?  null :  {paddingBottom: '408px'}}>
                <div className="block-movies__header">
                    <div className="title">
                        <h1>Recommendations</h1>
                    </div>
                    <ul className="categories genres-page__categories">
                        {categories.map((category) => {
                            const className = (pageState.mediaType === category.mediaType) ?  'category_active' : ''
                            return (
                                <li key={category.name}
                                    onClick={()=> switchMediaType(category.mediaType) }
                                    className={`category ${className}`}>{category.name}</li>
                            )
                        })}
                    </ul>
                </div>
                    <div className={((pageState.suggestions.length > 0))  ?  'preferences-group preferences-group_active' : 'preferences-group'}>
                        {pageState.addedValue &&
                        <div className="preference">
                            <span className='text'>{pageState.addedValue.name}</span>
                            <span className='close' onClick={() => deleteValue(pageState.addedValue.id)}>
                                            <ReactSVG svgClassName='close__icon' src={closeIcon}/>
                                        </span>
                        </div>}
                        {(!pageState.addedValue) &&
                        <input type="text" onChange={handleInput}
                               className={'preferences-input'}
                               placeholder='Enter your favorite movie or TV show'
                               value={pageState.query}/>}
                        {(pageState.suggestions.length > 0 && pageState.query) && <ul className="preference-suggestions">
                            {pageState.suggestions.slice(0,7).map((result) => {
                                const {
                                    originalTitle,
                                    year,
                                    mediaType,
                                    rating,
                                    id } = formMovieCardData(result)
                                return (
                                    <li className="preference-suggestion"
                                        key={id}
                                        onClick={() => addValue({id : id, name : originalTitle})}>
                                        <div className="title">{originalTitle}</div>
                                        <div className="info">
                                            <AddToFavoritiesBtn/>
                                            <AddToWishlistBtn/>
                                            <AddToWatchedBtn/>
                                            <PlayTrailerBtn/>
                                            {year && <div className="year">{year}</div>}
                                            <div className="stars">
                                                <ReactStars count={5} half={true} edit={false} size={20}
                                                            value={rating / 2}/>
                                            </div>

                                        </div>
                                    </li>
                                )
                            })}
                        </ul>}
                    </div>
                    <div className='movies-container'>
                        {pageState.recommendations.map((recommendation) => {

                            return (<MovieCard
                                key={recommendation.id}
                                data={recommendation}
                            />)
                        })}


                    </div>

                    { (pageState.totalPages > 1) &&
                    <Pagination
                        page={pageState.page}
                        totalPages={pageState.totalPages}
                        handlePageClick={handlePageClick}
                    /> }
                </div>

            </Container>
            <Footer/>

        </React.Fragment>
    )
}