import React from 'react'

import ReactSVG from 'react-svg'
import arrowIcon from '../../static/svg/arrow.svg'

import {MovieCard} from "../movie-card"

// functions

import {formMovieCardData} from "../../utils/functions"


export const BlockItems = (props) => {

    const data = props.items || []

    const movieCardsData = (props.page % 2 !== 0) ?  data.slice(0,10) : data.slice(10)

    return(
        <div className='block-movies'>
            <div className='block-movies__header'>
                <h1 className='title'>{props.blockTitle}</h1>
                <ul className='categories block-movies__categories'>
                    {props.categories.map((category) => (
                        <li key={category.categoryId} className={(props.category === category.categoryId) ?  'category category_active': 'category' } data-category-id={category.categoryId} onClick={props.changeCategory}>{category.categoryName}</li>
                    ))}
                </ul>
                <div className='controls'>
                    <div className={(props.page <= 1 ) ? 'control control-prev control__blocked' : 'control control-prev'} onClick={props.openPrevPage}><ReactSVG src={arrowIcon}  svgClassName='left control__icon'/></div>
                    <div className={(props.page >= 1000) ? 'control control-next control__blocked' : 'control control-next'} onClick={props.openNextPage}><ReactSVG src={arrowIcon}  svgClassName='right control__icon'/></div>
                </div>
            </div>
            <div className="movies-container">
                {movieCardsData.map((data) => {


                    return <MovieCard
                        key={data.id}
                        data={data}/>
                })}

            </div>
        </div>
    )
}