import React from 'react'
import { BrowserRouter, Switch, Route } from 'react-router-dom'
import { PersistGate } from 'redux-persist/integration/react'

//redux
import {Provider} from 'react-redux'
import {store, persistor} from "./utils/store"


//pages
import {Home} from "./pages/home"
import {Movie} from "./pages/movie"
import {TVShow} from "./pages/tv-show"
import {Genres} from "./pages/genres"
import {Discover} from "./pages/discover"
import {Recommendations} from "./pages/recommendations"
import {Movies} from "./pages/movies"
import {TVShows} from "./pages/tv-shows"
import {Lists} from "./pages/lists"

//modal
import{TrailersModal} from "./components/trailers-modal"


const App = () => {

    React.useEffect(() => {

        return () => localStorage.setItem('lol', 100)
    })

    return (
        <Provider store={store}>
            <PersistGate loading={null} persistor={persistor}>
                <BrowserRouter>
                    <Switch>
                        <Route exact path={'/'} component={Home}/>
                        <Route path={'/movie/:title/:id'} component={Movie}/>
                        <Route path={'/tv/:title/:id'} component={TVShow}/>
                        <Route path={'/genres'} component={Genres}/>
                        <Route path={'/discover'} component={Discover}/>
                        <Route path={'/recommendations'} component={Recommendations}/>
                        <Route path={'/movies'} component={Movies}/>
                        <Route path={'/tv_shows'} component={TVShows}/>
                        <Route path={'/lists'} component={Lists}/>
                    </Switch>
                </BrowserRouter>
                {/*Modal Trailer*/}
                <TrailersModal/>
            </PersistGate>
        </Provider>
    );

}

export default App;
