import React from 'react'

import {Header} from "../../components/header"
import {Container} from "../../components/container"
import {BannerSlider} from "../../components/banner-slider"
import {Genre} from "../../components/genre"
import {Footer} from "../../components/footer"

import {movieGenres} from "../../utils/constants"
import {tvGenres} from "../../utils/constants"





export const Genres = (props) => {
    const categories = ['Movies' , 'TV Shows']
    const [activeCategory, setCategory] = React.useState('Movies')
    const [genres, setGenres] = React.useState(movieGenres)
    const [mediaType, setMediaType] = React.useState('movie')

    const switchCategory = (e) => {
        const currentCateg = e.currentTarget.dataset.category
        setCategory(currentCateg)

        switch (currentCateg){
            case 'Movies':
                setGenres(movieGenres)
                setMediaType('movie')
                break;
            case 'TV Shows':
                setGenres(tvGenres)
                setMediaType('tv')
                break;

        }


    }
    return (
        <React.Fragment>
            <Header/>

            <Container>
                <div className='genres-page'>
                    <div className="block-movies__header">
                        <div className="title">
                            <h1>Genres</h1>
                        </div>
                        <ul className="categories genres-page__categories">
                            {categories.map((category) => {
                                const className = (activeCategory === category) ?  'category_active' : ''
                                return (
                                    <li key={category}
                                        className={`category ${className}`}
                                        onClick={switchCategory}
                                        data-category={category}>{category}</li>
                                )
                            })}
                        </ul>
                    </div>
                    <div className="genres">
                        {genres.map((genre) => {
                            return (
                                <Genre
                                    key={genre.name+genre.id}
                                    genre={genre}
                                    mediaType={mediaType}
                                />
                            )
                        })}
                    </div>
                </div>
            </Container>
            <Footer/>
        </React.Fragment>

    )
}