import React from 'react';
import Slider from 'react-slick'

import ReactSVG from 'react-svg'

import placeholder from '../../static/images/placeholder180x270.png'

import arrowIcon from '../../static/svg/arrow.svg'

const NextArrow = (props) =>(
    <div className={'genre-arrow arrow-next '} onClick={props.onClick}>
        <ReactSVG src={arrowIcon} svgClassName='genre-arrow__icon'/>
    </div>
)
const PrevArrow = (props) =>{
    return (<div className={' genre-arrow arrow-prev'}  onClick={props.onClick}>
        <ReactSVG src={arrowIcon} svgClassName='genre-arrow__icon'/>
    </div>)}







export const CastSlider = (props) => {
    const settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 4,
        slidesToScroll: 6,
        draggable: true,
        arrows: true,
        nextArrow: <NextArrow/>,
        prevArrow: <PrevArrow/>,
    }
    return (<React.Fragment>

            { (props.cast.length >= 4 ) ?
            <Slider {...settings} className={'cast-slider'}>
                {props.cast.map((actor) => {
                    const src = (actor.profile_path) ? `http://image.tmdb.org/t/p/w185${actor.profile_path}` : placeholder
                    return (
                        <div className='cast-item' key={actor.name}>
                            <img src={src} alt=""/>
                            <div>{actor.name}</div>
                        </div>
                    )
                })}
            </Slider>
                :
                <div className='cast-items'>
                    {props.cast.map((actor) => {
                    const src = (actor.profile_path) ? `http://image.tmdb.org/t/p/w185${actor.profile_path}` : placeholder
                    return (
                        <div className='cast-item' key={actor.name}>
                            <img src={src} alt=""/>
                            <div>{actor.name}</div>
                        </div>
                    )
                })}
                </div>}
        </React.Fragment>
    )


}