import React from 'react';
import {connect} from 'react-redux'

import ReactSVG from 'react-svg'
import favoritiesIcon from '../../static/svg/like.svg'

import {store} from "../../utils/store";
import {ActionTypes } from "../../utils/constants";



export const AddToFavoritiesBtn_ = (props) =>{

    const data = {...props.data, mediaType: props.mediaType}


    const isActive = (data) => {
        return props.favorities.some((item) => {
            return item.id === data.id
        })
    }
    const active = isActive(data)

    const addToFavorities = (data) => {
        if (isActive(data)) {
            store.dispatch({
                type: ActionTypes.REMOVE_FROM_FAVORITIES,
                item: data
            })

            return;
        }
        store.dispatch({type: ActionTypes.ADD_TO_FAVORITIES, item: data })

    }

    return (<div className={(active) ? "add-to-list add-to-favorities add-to-list_active" : "add-to-list add-to-favorities"}
                 onClick={()=>addToFavorities(data)}>
        <ReactSVG src={favoritiesIcon} svgClassName={'action-icon add-to-favorities__icon '}/>
    </div>)
}

const mapStateToProps = (state)=>{
    return state.lists
}

export const AddToFavoritiesBtn = connect(mapStateToProps)(AddToFavoritiesBtn_)