import React from 'react';

import {Header} from "../../components/header";
import {Container} from "../../components/container";
import {API_KEY} from "../../utils/constants";
import {formMovieCardData} from "../../utils/functions";
import {MovieCard} from "../../components/movie-card";
import {Pagination} from "../../components/pagination";
import {Footer} from "../../components/footer"

export const TVShows = (props) => {

    const reducer = (prevState, newState) => {
        return {...prevState, ...newState}
    }
    const categoriesTV = [
        {categoryName: 'On The Air', categoryId: 'on_the_air'}, {
            categoryName: 'Popular',
            categoryId: 'popular'
        }, {categoryName: 'Top rated', categoryId: 'top_rated'}]

    const initialState = {
        page: 1,
        currentCategory: categoriesTV[0].categoryId,
        totalPages: undefined,
        movies: [],
    }

    const [pageState, dispatch] = React.useReducer(reducer, initialState)


    const switchCategory = (category) => {
        dispatch({...initialState, currentCategory: category})
    }


    React.useEffect(() => {
        fetch(`https://api.themoviedb.org/3/tv/${pageState.currentCategory}?api_key=${API_KEY}&language=en-US&page=${pageState.page}`)
            .then((response) => {
                if (response.ok) {
                    return response.json()
                }
                throw new Error(`Cannot connect to the server to get items `)
            }).then((data) => dispatch({movies: data.results, totalPages: data.total_pages}))
    }, [
        pageState.currentCategory,
        pageState.page,
    ])

    const handlePageClick = ({selected}) => {
        dispatch({page: selected + 1})
    }

    return (
        <React.Fragment>
            <Header/>
            <Container>
                <div className="movies-page">
                    <div className="block-movies__header">
                        <div className="title">
                            <h1>TV Shows</h1>
                        </div>
                        <ul className="categories genres-page__categories">
                            {categoriesTV.map((category) => (
                                <li key={category.categoryId}
                                    className={(pageState.currentCategory === category.categoryId) ? 'category category_active' : 'category'}
                                    onClick={() => switchCategory(category.categoryId)}>
                                    {category.categoryName}
                                </li>
                            ))}
                        </ul>
                    </div>
                    <div className='movies-container'>
                        {pageState.movies.map((movie) => {


                            return (
                                <MovieCard
                                    id={movie.id}
                                    data={movie}
                                />)
                        })}


                    </div>
                    {pageState.totalPages &&
                    <Pagination
                        handlePageClick={handlePageClick}
                        page={pageState.page}
                        totalPages={pageState.totalPages}
                    />}
                </div>
            </Container>
            <Footer/>
        </React.Fragment>
    )
}