import React from 'react';




export const Banner = (props) => {
    return (
        <div className={`banner ${props.extraClasses || ''}`}  style={{ backgroundImage:`url(https://image.tmdb.org/t/p/original${props.backdrop_path}`}} >
            <div className="banner__title">
                <div className='title-text'>{props.title}</div>
            </div>
        </div>
    )
}