import React from 'react'
import ReactSVG from 'react-svg'

import poweredIcon from '../../static/svg/powered-by-tmdb.svg'
import githubIcon from '../../static/svg/github-logo.svg'
import {Container} from "../container";

export const Footer = () => {




    return (
        <footer className='footer'>
            <Container>
                <div className="tech-links">
                    <a href="#"><ReactSVG src={githubIcon} svgClassName='github-icon'/></a>
                    <a href="themoviedb.org"><ReactSVG src={poweredIcon} svgClassName='powered-icon'/></a>
                </div>
            </Container>
        </footer>
    )}