import React from 'react';
import ReactModal from 'react-modal'


import {store} from "../../utils/store"
import {connect} from 'react-redux'



import ReactSVG from 'react-svg'
import crossIcon from '../../static/svg/close-cross.svg'
import {ActionTypes} from "../../utils/constants";


// modal styles
const overlay = {
    zIndex : '10',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    backgroundColor: 'rgba(0, 0, 0, 0.7)'
}
const content = {
    overflow: 'visible',
    padding: 0,
    top: 'auto',
    right: 'auto',
    bottom: 'auto',
    left: 'auto',
}
ReactModal.setAppElement('#root')


export const TrailersModal_ = (props)=> {

    const closeModal = () => {
        store.dispatch({type: ActionTypes.HIDE_MODAL })
    }

    const switchVideo = (e) => {
        const target = e.currentTarget
        const currentVideoKey = target.dataset.key
        store.dispatch({type: ActionTypes.SET_VIDEO_KEY, currentVideoKey: currentVideoKey})
    }


    const data = props.trailersData

    const windowWidth = document.documentElement.clientWidth

    const currentVideoKey = props.currentVideoKey



    let switchOptions = 0
    const videoWidth = ( windowWidth >= 1400 ) ? windowWidth*0.6 : 880

    const  videoHeight = ( windowWidth >= 1400 ) ? windowWidth*0.34 :  500


    return (
        <ReactModal
            htmlOpenClassName="fixed"
            style={{ overlay, content}}
            onRequestClose={closeModal}
            isOpen={props.showModal}>
            <div>
                <div className='contollers'>
                    <div className="switch-video" >
                        {Object.entries(data).length && Object.entries(data).map((video) => {
                            return video[1].map((key, i) => {
                                if (switchOptions < 4) {
                                    switchOptions++
                                    return (<div key={key}
                                                 className={key === currentVideoKey ? "switch-video__item switch-video__item_active" : 'switch-video__item'}
                                                 onClick={switchVideo} data-key={key}>
                                        {video[0]} {i + 1}
                                    </div>)
                                }
                                return null

                            })
                        })}
                    </div>
                    <div className="close-modal" onClick={closeModal}>
                        <ReactSVG src={crossIcon} svgClassName='close-modal__icon'/>
                    </div>
                </div>
                <iframe src={`http://www.youtube.com/embed/${currentVideoKey}` } title={'youtube videos (trailers)'}
                        width={videoWidth} height={videoHeight}  frameBorder="0" allowFullScreen/>
            </div>
        </ReactModal>

    )
}

const mapStateToProps = (state)=>{
    return state.trailersModal
}

export const TrailersModal = connect(mapStateToProps)(TrailersModal_);