import React from 'react';
import Slider from "react-slick";

import {Banner} from "../banner";
import {Link} from 'react-router-dom';

import {formMovieCardData} from "../../utils/functions";


export const BannerSlider = (props)=> {
    const settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        draggable: true,
        arrows: false,
        autoplay: true,
    }
    const [slides, setSlides ] = React.useState(props.slides)
    React.useEffect(()=>{
        setSlides(props.slides)
    }, [props.slides.length])


    return (props.slides.length) ?  (
        <Slider {...settings} className='banner-slider'>
            {slides.map((slide) => {

                const { title, originalTitle, mediaType } = formMovieCardData(slide)

                return (
                    <Link className={'banner-slider__item'}
                          key={originalTitle}
                          to={{
                              pathname: `/${mediaType}/${slide.id}/${originalTitle.toLowerCase().replace(/\s+/g, '_')}`,
                          }}
                    >
                        <Banner backdrop_path={slide.backdrop_path} title={originalTitle}/>
                    </Link>
                )
            })}
        </Slider>) : null
}
