
import React from 'react';
import {movieGenres} from "../../utils/constants";
import {tvGenres} from "../../utils/constants";

import InputRange from 'react-input-range';

import ReactSVG from 'react-svg'
import closeIcon from '../../static/svg/close-cross.svg'

import {Discover} from "../../pages/discover";
import {DropdownSortBy} from "./dropdown-sort-by";
import {DropdownKeywords} from "./dropdown-keywords";
import {DropdownGenres} from "./dropdown-genres";
import {YearSettings} from "./year-settings";



export const DiscoverForm = (props) => {
    const currentYear = new Date().getFullYear()


    const sortByOptions = [
        {
            name: 'Popularity Descending',
            id: 'popularity.desc'
        },
        {
            name: 'Popularity Ascending',
            id: 'popularity.asc'
        },
        {
            name: 'Rating Descending',
            id: 'vote_average.desc'
        },
        {
            name: 'Rating Ascending',
            id: 'vote_average.asc'
        },
        {
            name: 'Release Date Descending',
            id: 'primary_release_date.desc'
        },
        {
            name: 'Release Date Ascending',
            id: 'primary_release_date.asc'
        },
    ]

    const changeYears = (value) => {
        props.dispatch({year: value})
        props.goToFirstPage()
    }

    return (
        <form className='discover-form'>
            <div className="discover-form__element">
                <div className='label'> Sort by</div>
                <DropdownSortBy
                    goToFirstPage={props.goToFirstPage}
                    sortBy={props.sortBy}
                    dispatch={props.dispatch}
                    sortByOptions={sortByOptions}
                />
            </div>
            <div className="discover-form__element">
                <div className='label'>Keywords</div>
                <DropdownKeywords
                    goToFirstPage={props.goToFirstPage}
                    dispatch={props.dispatch}
                    keywords={props.keywords}

                />
            </div>
            <div className="discover-form__element">
                <div className='label'>Genres</div>
                <DropdownGenres
                    goToFirstPage={props.goToFirstPage}
                    suggestions={(props.mediaType === 'tv') ? tvGenres : movieGenres}
                    dispatch={props.dispatch}
                    genres={props.genres}
                />
            </div>
            <div className="discover-form__element">
                <div className="input-range__header">
                    <div className='label'>Release year</div>
                    <YearSettings
                        radioState={props.radioState}
                        handleOptionChange={props.handleOptionChange}
                    />
                </div>
                <InputRange
                    maxValue={currentYear}
                    minValue={1900}
                    value={props.rangeValues}
                    onChange={value => props.setRangeValues(value)}
                    onChangeComplete={changeYears}
                />
            </div>
        </form>
    )
}