import React, {Fragment} from 'react';

import {Header} from "../../components/header";
import {Container} from "../../components/container";
import {Footer} from "../../components/footer";

import {store} from "../../utils/store"
import {connect} from 'react-redux'
import {TrailersModal_} from "../../components/trailers-modal"
import {MovieCard} from "../../components/movie-card"
import {API_KEY} from "../../utils/constants";

const reducer = (prevState, newState) => {
    return {...prevState, ...newState}
}

export const Lists_ = (props) => {
    const listTypes = ['favorities', 'wishlist', 'watched']
    const mediaTypes = [ {id: 'all', name: 'All'} , {id: 'tv', name: 'TV Shows'}, {id: 'movie', name: 'Movies'}]

    const initialState = {
        currListType: listTypes[0],
        currMediaType: mediaTypes[0],
        favorities: [],
    }

    const [pageState, dispatch] = React.useReducer(reducer, initialState)

    const changeList = (currListType) => {
        dispatch({currListType})
    }
    const changeMediaType =(currMediaType) => {
        dispatch({currMediaType})
    }

    React.useEffect(()=>{

    }, [props.favorities.length])


    const cards = props[pageState.currListType].filter((item) => {
        const mediaType = item.first_air_date ? 'tv'  :  'movie'

        return ((pageState.currMediaType.id === 'all') ? true : mediaType === pageState.currMediaType.id)
    })

    return (
        <Fragment>
            <Header/>
            <Container>
                <div className="lists-page">
                    <div className='block-movies__header'>
                        <h1 className='title'>Lists</h1>
                        <div className='categories-block'>
                            <ul className='categories block-trending__categories'>
                                {listTypes.map((listType) => {
                                    const className = (pageState.currListType === listType) ? 'category category_active category' : 'category'
                                    return (
                                        <li className={className}
                                            key={listType}
                                            onClick={() => changeList(listType)}>
                                            {listType}
                                        </li>)
                                })}

                            </ul>
                            <ul className='categories block-trending__categories'>
                                {mediaTypes.map((mediaType) => {
                                    const className = (pageState.currMediaType.id === mediaType.id) ? 'category category_active category' : 'category'
                                    return (
                                        <li className={className}
                                            key={mediaType.id}
                                            onClick={() => changeMediaType(mediaType)}>
                                            {mediaType.name}
                                        </li>)
                                })}
                            </ul>
                        </div>
                    </div>
                    { (cards.length > 0 ) ?
                        <div className="movies-container" style={(cards.length <= 5) ? {marginBottom: '126px'} : null}>
                        {cards

                            .map((item)=>{
                                return(
                                    <MovieCard data={item} key={item.id} />
                                )
                            })}
                    </div> :
                         <div className='lists-placeholder'>
                             <div className="lists-placeholder__text">
                                 There are no added movies or TV shows to this list yet
                             </div>
                         </div>
                    }
                </div>
            </Container>
            <Footer/>
        </Fragment>
    )
}

const mapStateToProps = (state)=>{
    return state.lists
}

export const Lists = connect(mapStateToProps)(Lists_);