import React from 'react'

import Slider from 'react-slick'


import {MovieCard} from "../movie-card"
import {Controls} from "../controls"
import {formMovieCardData} from "../../utils/functions"


export const SimilarMovies = (props)=>{
    const slider = React.useRef()
    const controlPrev = React.useRef({current: undefined})
    const controlNext = React.useRef({current: undefined})
    const [currentSlide, setCurrentSlide] = React.useState(0)

    const settings = {
        dots: true,
        infinite: false,
        speed: 500,
        slidesToShow: 5,
        slidesToScroll: 5,

        beforeChange: (oldIndex, newIndex) => {setCurrentSlide(newIndex/5)}
    }
    const goToNextSlide = () => {
        slider.current.slickNext()
    }
    const goToPrevSlide = () => {
        slider.current.slickPrev()
    }
    React.useEffect(()=>{
        slider.current.slickGoTo(0)
    }, [props.id])

    return (
        <div className="similar-movies">
            <div className='section-header'>
                <h1>{props.name}</h1>
                <Controls next={goToNextSlide}
                          prev={goToPrevSlide}
                          controlPrev={controlPrev}
                          controlNext={controlNext}
                          extraClasses={'similar-movies__controls'}
                          controlPrevBlocked={(currentSlide === 0)}
                          controlNextBlocked={(currentSlide === 3)}

                />
            </div>

            <Slider {...settings} ref={slider}>
                {props.similarMovies.map((movieCardInfo) => {

                    return <MovieCard data={movieCardInfo}
                                      extraClasses={{'movie-card':'width-auto'}}
                                     />
                })}
            </Slider>
        </div>)
}