import React from 'react';

import ReactPaginate from 'react-paginate'
import ReactSVG from 'react-svg'

import {Header} from "../../components/header"
import {Container} from "../../components/container"

import {DiscoverForm} from "../../components/discover-form"
import {API_KEY} from "../../utils/constants"
import {MovieCard} from "../../components/movie-card"
import {formMovieCardData} from "../../utils/functions"

import {Footer} from "../../components/footer"

import {Pagination} from "../../components/pagination"




const reducer = (prevState, newState) => {
    return {...prevState, ...newState}
}

export const Discover = (props) => {
    let mounted = React.useRef(false)
    const currentYear = new Date().getFullYear()
    const categories = [
        {
            name:  'Movies',
            mediaType: 'movie'
        },
        {
            name:  'TV shows',
            mediaType: 'tv'
        }]

    const initialState = {
        results: [],
        mediaType: 'movie',
        sortBy: {
            id: 'popularity.desc',
            name: 'Popularity Descending'
        },
        totalPages: undefined,
        page: 1,
        keywords: [],
        genres: [],
        year:  {
            min: 1900,
            max: currentYear
        }
    }


    const [discoverState, dispatch] = React.useReducer(reducer ,initialState )

    const resetDiscover = () => {
        dispatch({...initialState})
        setRadioState(initialRadioState)
        setRangeValues(initialRangeState)
    }
    const goToFirstPage = () =>{
        dispatch({page: 1})
    }



    const setYearRange = (year) =>{
        dispatch({year})
    }
    /* radio  component  (years) */
    const initialRadioState = {
        selectedOption: 'range'
    }
    const [radioState, setRadioState] = React.useState(initialRadioState)

    const initialRangeState =  {
                max: currentYear,
                min: 1900
            }

    const initialSingleState = currentYear

    const handleOptionChange = (e) => {
        setRadioState({
            selectedOption: e.target.value
        })

            if(e.target.value === 'range'){
                setRangeValues(initialRangeState)
                dispatch({year: initialRangeState})
            } else {
                setRangeValues(initialSingleState)
                dispatch({year: initialSingleState})
            }

    }

    const [rangeValues, setRangeValues] = React.useState(initialRangeState)


    React.useEffect(()=>{
        if (props.location && !mounted.current){
            dispatch(props.location.state)
            mounted.current = true

        }

        const year = (discoverState.mediaType === 'movie') ?
            `primary_release_date.gte=${(discoverState.year.min) ? discoverState.year.min : discoverState.year }-01-01&primary_release_date.lte=${( discoverState.year.max ) ? discoverState.year.max : discoverState.year }-12-30` :
            `first_air_date.gte=${(discoverState.year.min) ? discoverState.year.min : discoverState.year }-01-01&first_air_date.lte=${( discoverState.year.max ) ? discoverState.year.max : discoverState.year }-12-30`
        const keywordsIds = discoverState.keywords.map((keyword) => (keyword.id))
        const genresIds = discoverState.genres.map((genre) => (genre.id))

        const query = `https://api.themoviedb.org/3/discover/`
            +`${discoverState.mediaType}?`
            +`api_key=${API_KEY}&language=en-US&`
            +`sort_by=${discoverState.sortBy.id}&`
            +`include_adult=false&include_video=false&`
            +`page=${discoverState.page}&`
            +`with_keywords=${keywordsIds}&`
            +`with_genres=${genresIds}&`
            +year


        fetch(query)
            .then((response) => {
                if (response.ok){
                    return response.json()
                }
                throw new Error(`Cannot connect to the server to get items `)
            }).then((data)=> {
                dispatch({results: data.results , totalPages: data.total_pages})
            })

    }, [discoverState.mediaType,
        discoverState.sortBy.id,
        discoverState.page,
        discoverState.keywords.length,
        discoverState.genres.length,
        discoverState.year.min,
        discoverState.year.max,
        discoverState.year,
        rangeValues,
        rangeValues.min,
        rangeValues.max,
        discoverState.totalPages,
        discoverState.results.length
    ])



    const handlePageClick = ({selected}) =>{
        dispatch({page: selected + 1})
        window.scrollTo(0,0)
    }


    const switchMediaType = (mediaType) =>{

        resetDiscover()
        dispatch({mediaType})
    }


    return (
        <React.Fragment>
            <Header/>
            <Container>
                <div className="discover-page">
                    <div className="block-movies__header">
                        <div className="title">
                            <h1>Discover</h1>
                        </div>
                        <ul className="categories genres-page__categories">
                            {categories.map((category) => {
                                const className = (discoverState.mediaType === category.mediaType) ?  'category_active' : ''
                                return (
                                    <li key={category.name}
                                        onClick={()=> switchMediaType(category.mediaType) }
                                        className={`category ${className}`}>{category.name}</li>
                                )
                            })}
                        </ul>
                    </div>
                    <DiscoverForm
                        setRangeValues={setRangeValues}
                        rangeValues={rangeValues}
                        dispatch={dispatch}
                        goToFirstPage={goToFirstPage}
                        {...discoverState}

                        radioState={radioState}
                        handleOptionChange={handleOptionChange}
                    />
                    <div className="discover-results">
                        {discoverState.results.map((result) => {

                            return (
                                <MovieCard

                                    data={result}

                                />
                            )
                        })}
                    </div>
                    {(discoverState.results.length > 0) &&
                    <Pagination
                        page={discoverState.page}
                        totalPages={discoverState.totalPages}
                        handlePageClick={handlePageClick}
                    />
                    }
                </div>
            </Container>
            <Footer/>
        </React.Fragment>
    )
}