import React from 'react';
import {Link} from 'react-router-dom';


export const Genre = (props) => {
    const className = props.genre.name.toLowerCase().replace(/\s+|\&/g,'-')

    return (
        <Link to={{
            pathname: `/discover`,
            state: {
                genres: [props.genre],
                mediaType: props.mediaType
            }
        }}
             className={`genre genre-${className}`}>
            <div className='genre-overlay'>
                <div className="genre__title">{ props.genre.name.replace(/\&/, '\n&\n')}</div>
            </div>
        </Link>
    )
}