import React from 'react';

import ReactSVG from 'react-svg'
import arrowIcon from '../../static/svg/arrow.svg'

import {MovieCard} from "../movie-card"

import {formMovieCardData} from "../../utils/functions"



export const Trending = (props)=> {
    const data = props.items || []
    const movieCardsData = (props.page % 2 !== 0) ?  data.slice(0,10) : data.slice(10,20)
    const controlPrev = React.useRef(null)
    const controlNext = React.useRef(null)




    return (
        <div className={'block-trending'}>
            <div className='block-movies__header'>
                <h1 className='title'>Trending</h1>
                <div className='categories-block'>
                    <ul className='categories block-trending__categories'>
                        {props.categories['time-window'].map((subCategory) => {
                            const className = (props['time-window'] === subCategory.categoryId) ? 'category category_active category' : 'category'
                            return (<li className={className}
                                        key={subCategory.categoryId}
                                        onClick={props.changeCategory}
                                        data-category-type='time-window'
                                        data-category-id={subCategory.categoryId}>{subCategory.categoryName}</li>)
                        })}

                    </ul>
                    <ul className='categories block-trending__categories'>
                        {props.categories['media-type'].map((subCategory) => {
                            const className = (props['media-type'] === subCategory.categoryId) ? 'category category_active category' : 'category'
                            return (<li className={className}
                                        key={subCategory.categoryId}
                                        onClick={props.changeCategory}
                                        data-category-type='media-type'
                                        data-category-id={subCategory.categoryId}>{subCategory.categoryName}</li>)
                        })}
                    </ul>
                </div>
                <div className='controls'>
                    <div className={(props.page <= 1 )  ? 'control control-prev control__blocked' : 'control control-prev'} onClick={props.openPrevPage}><ReactSVG src={arrowIcon}  svgClassName='left control__icon'/></div>
                    <div className={(props.page >= 1000) ? 'control control-next control__blocked' : 'control control-next' } onClick={props.openNextPage}><ReactSVG src={arrowIcon}  svgClassName='right control__icon'/></div>
                </div>
            </div>
            <div className="trending-container">
                {movieCardsData.map((movieCardInfo, i) => {

                    const formedData = formMovieCardData(movieCardInfo)


                    let extraClasses
                    let shortTitle

                    if (i === 0 || i === (movieCardsData.length - 1)) {
                        shortTitle = (formedData.originalTitle.length <= 14) ? formedData.originalTitle : formedData.originalTitle.slice(0, 22) + '...'
                        extraClasses = {'movie-card': 'movie-card_lg'}
                    } else {
                        extraClasses = {'movie-card': 'movie-card_sm'}
                        shortTitle = (formedData.originalTitle.length <= 14) ? formedData.originalTitle : formedData.originalTitle.slice(0, 14) + '...'
                    }

                    return (<MovieCard data = {movieCardInfo}
                                       key={movieCardInfo.id}
                                       shortTitle={shortTitle}
                                       playTrailers={props.playTrailers}
                                       extraClasses={extraClasses}/>)
                })}


            </div>
        </div>
    )
}