import React, {Fragment} from 'react'


import {Footer} from "../../components/footer"
import {Header} from "../../components/header"
import {Container} from "../../components/container"
import {BannerSlider} from "../../components/banner-slider"
import {BlockItems} from "../../components/block-items"
import {GenresBlock} from "../../components/block-genres";
import {Trending} from "../../components/block-trending";


import {API_KEY, movieGenres, tvGenres} from "../../utils/constants";

const reducer = (prevState, newState) => {
    return {...prevState, ...newState}
}

function useBlockController(type, category = '') {
    const [blockState, dispatch] = React.useReducer(reducer, {
        page: 1,
        items: [],
        category,
        'media-type': 'all',
        'time-window': 'day'

    })
    const openPrevPage = () => {
        if (blockState.page > 1){
            dispatch({page:  blockState.page - 1  })
        }
    }
    const openNextPage = () => {
        if (blockState.page <= 1000){
            dispatch({page:  blockState.page + 1  })
        }
    }
    const changeCategory = (e) =>{
        // For Trending Only
        if (e.target.dataset.categoryType) {
            dispatch({[e.target.dataset.categoryType]: e.target.dataset.categoryId, page: 1})
            return;
        }
        //For the other cases
        dispatch({category: e.target.dataset.categoryId, page: 1})
    }





    React.useEffect(()=>{
        const category =  type === 'trending' ? `${blockState['media-type']}/${blockState['time-window']}` : blockState.category
        fetch(`https://api.themoviedb.org/3/${type}/${category}?api_key=${API_KEY}&language=en-US&page=${Math.ceil(blockState.page/2)}`)
            .then((response) => {
            if (response.ok){
                return response.json()
            }
            throw new Error(`Cannot connect to the server to get items `)
        }).then((data)=> dispatch({items: data.results}))

    }, [blockState.page, blockState.category, blockState['media-type'],   blockState['time-window']])

    return {openPrevPage, openNextPage, changeCategory, ...blockState }
} // add number of shown cards






export const Home = () => {

    const categoriesTrending = {
        'time-window': [
            {categoryName: 'Today', categoryId: 'day'},
            {categoryName: 'This week', categoryId: 'week'},
        ],
        'media-type':  [
            {categoryName: 'All', categoryId: 'all'},
            {categoryName: 'Movies', categoryId: 'movie'},
            {categoryName: 'TV Shows', categoryId: 'tv'}],
    }
    const categoriesMovies = [
        {categoryName: 'New', categoryId: 'now_playing'},
        {categoryName: 'Popular', categoryId: 'popular'},
        {categoryName: 'Top rated', categoryId: 'top_rated'}]

    const categoriesTV = [
        {categoryName: 'On The Air', categoryId: 'on_the_air'}, {
        categoryName: 'Popular',
        categoryId: 'popular'
    }, {categoryName: 'Top rated', categoryId: 'top_rated'}]


    const movieBlockState = useBlockController('movie','now_playing' )
    const tvBlockState = useBlockController('tv','on_the_air' )
    const trendingBlockState = useBlockController('trending')






    return (
        <Fragment>
            <Header/>
            <BannerSlider
                slides={trendingBlockState.items}
            />
            <Container>
                <div className='main'>
                    {/* Trending      //*/}
                    <Trending categories={categoriesTrending}
                              {...trendingBlockState}/>
                    {/* Movies      //*/}
                    <BlockItems blockTitle={'Movies'}
                                categories={categoriesMovies}
                                {...movieBlockState}/>
                    {/*Genres*/}
                    <GenresBlock
                        name={'Movie Genres'}
                        genres={movieGenres}
                        mediaType={'movie'}
                    />
                    {/*  TV Shows}/*/}
                    <BlockItems blockTitle={'TV Shows'}
                                categories={categoriesTV}
                                {...tvBlockState}/>
                    <GenresBlock
                        name={'TV Genres'}
                        genres={tvGenres}
                        mediaType={'tv'}
                    />

                </div>
            </Container>
            <Footer/>
        </Fragment>)
}