import placeholder40x60 from '../static/images/placeholder40x60.png'


function isNew(date) {
    const now = new Date()
    return Date.parse(date) > now.setMonth(now.getMonth() - 4)
}


export function formMovieCardData(movieCardInfo){

    const {title, name, original_title, original_name, adult, vote_average, overview, poster_path, release_date, first_air_date, id} = movieCardInfo


    let date = release_date || first_air_date || ''

    const mediaType = first_air_date ? 'tv'  :  'movie'

    const originalTitle = title || name || original_title || original_name

    const shortTitle = (originalTitle.length <= 14) ? originalTitle :  originalTitle.slice(0,14) + '...'

    const year = date.slice(0,4)

    const is_new = isNew(date)

    const poster = (poster_path) ?  `https://image.tmdb.org/t/p/w500${poster_path}` : 'https://via.placeholder.com/190x285.png'

    const formedData = {
        key: id,
        originalTitle: originalTitle,
        shortTitle : shortTitle,
        year: year,
        new: is_new,
        mediaType: mediaType,
        rating: Math.round(vote_average),
        adult,
        poster: poster,
        id: id
    }

    return formedData

}


export function formMovieStateData(movieState, credits) {

    const {backdrop_path , vote_average, budget,
        revenue, genres, original_language,
        production_countries, id,
        production_companies, release_date,
        tagline, overview } = movieState

    if (!movieState.id) return {}

        const year = release_date.slice(0,4)

        const title = movieState.title || movieState.original_title

        const poster_path =  (movieState.poster_path) ? `https://image.tmdb.org/t/p/w500${movieState.poster_path}` : 'https://via.placeholder.com/280x390.png'

        const tmdbScore = vote_average

        const country  = production_countries[0] ? production_countries[0]['iso_3166_1'] : null

        const directors = credits['crew']
            .filter((member) => {return (member["job"] === "Director")})
            .map((member, i) => {
                return (i === 0) ? `${member['name']}` : `, ${member['name']} `})


    return {backdrop_path , vote_average, budget,
        revenue, genres, original_language,
        production_countries, id, production_companies,
         title, poster_path, tmdbScore,
        country, directors, year, tagline, overview}
}


export function formTvStateData(tvShow, credits) {

    if (!tvShow.id)  return {}

    const  {backdrop_path,  vote_average, id, origin_country, original_language, genres, overview, first_air_date, production_companies, seasons, original_name, name} = tvShow


    const year = first_air_date.slice(0,4)


    const poster_path = (tvShow.poster_path) ? `https://image.tmdb.org/t/p/w500${tvShow.poster_path}` : 'https://via.placeholder.com/280x390.png'

    const title = tvShow.name || tvShow.original_name


    const tmdbScore = vote_average

    const country = origin_country



    return {
        backdrop_path,
         genres, original_language,
        id, production_companies,
        title, poster_path, tmdbScore,
        country,  year, overview, seasons
    }
}


export const removeItemFromList = (arr , {id , mediaType}) => {
    const filteredItems = arr.filter(elem => {
        if (elem.id === +id){
            return (elem.mediaType !== mediaType)
        } return true
    })
    return filteredItems
}

export const  formSearchSuggestionData = (data) =>{
    const { poster_path,  profile_path,  original_title, title, original_name, name, media_type, release_date, first_air_date, vote_average, id} = data
    const mediaType = media_type
    const image = (profile_path) ? `https://image.tmdb.org/t/p/w92/${profile_path}`
        : (poster_path) ?  `https://image.tmdb.org/t/p/w92/${poster_path}`
            :  placeholder40x60
    const date =  first_air_date || release_date
    const dateObj = new Date(date)

    const year =  (date) ?  dateObj.getFullYear() : null
    const rating =  (vote_average)  ? Math.round(vote_average) / 2 : null
    let caption = original_title || title || name || original_name || name
    caption = (caption.length > 20 ) ?  `${caption.slice(0, 20)}...` : caption

    return {
        id,
        mediaType,
        year,
        rating,
        image,
        caption
    }

}

