import React from 'react';

import ReactSVG from 'react-svg'
import closeIcon from '../../static/svg/close-cross.svg'
import {API_KEY} from "../../utils/constants"



export const DropdownKeywords = (props) => {

    const [keywordsSuggestions, setKeywords] = React.useState([])
    const [query , setQuery] = React.useState('')
    const [active, setActive] = React.useState(false)

    const handleInput = (e) => {
        setQuery(e.target.value)
        if (e.target.value){
            setActive(true)
            return;
        }
        setActive(false)
    }
    const selectOption = (suggestion) =>{
        setActive(false)
        props.goToFirstPage()
        props.dispatch({keywords: [...props.keywords, suggestion]})
        setQuery('')
    }

    const deleteKeyword = (keyword) => {
        const keywords = props.keywords.slice().filter(elem => elem.id !== keyword.id)
        props.dispatch({keywords})
    }


    React.useEffect(()=>{
        if(!query) return;
        fetch(`https://api.themoviedb.org/3/search/keyword?api_key=${API_KEY}&query=${query}&page=1`)
            .then((response) => {
                if (response.ok){
                    return response.json()
                }
                throw new Error(`Cannot connect to the server to get items `)
            }).then((data)=> {
                setKeywords (data.results)})
    }, [query])


    return (
        <div className={(active) ? 'dropdown dropdown_active' : 'dropdown' }>
            <div className='input-group'>
                <ul className="input-group__added-values">
                    {props.keywords.map((keyword)=> {
                        return (
                            <li className="input-group__added-value" key={keyword.id}>
                                <span>{keyword.name}</span>
                                <span className='close' onClick={()=> deleteKeyword(keyword)}>
                                                <ReactSVG svgClassName='close__icon' src={closeIcon} />
                                            </span>
                            </li>
                        )
                    })}

                </ul>
                <input type="text" onChange={handleInput} value={query} className='input-group__input' placeholder='Filter by keywords...'/>

            </div>
            <ul  className='dropdown-select' style={(keywordsSuggestions.length <= 4) ? {height: 'auto'} : {}}>
                {keywordsSuggestions.map((suggestion) => {

                    return (<li key={suggestion.name} onClick={() => selectOption(suggestion)} className='dropdown-select__option' data-value="popularity.desc" >{suggestion.name}</li>)
                })}
            </ul>
        </div>
    )
}