import React from 'react';
import ReactPaginate from 'react-paginate'
import ReactSVG from 'react-svg'

import arrowIcon from '../../static/svg/arrow.svg'

const NextArrow = (<ReactSVG src={arrowIcon} svgClassName='arrow-next arrow-icon'/>)

const PrevArrow = (<ReactSVG src={arrowIcon} svgClassName='arrow-prev arrow-icon'/>)

export const Pagination = (props) => {

    return (
        <div className="pagination__wrapper">
            <ReactPaginate
                previousLabel={PrevArrow}
                nextLabel={NextArrow}
                containerClassName={'pagination'}
                pageClassName={'page'}
                pageCount={(props.totalPages >= 1000) ? 1000 : props.totalPages}
                forcePage={props.page - 1}
                pageRangeDisplayed={6}
                marginPagesDisplayed={2}
                onPageChange={props.handlePageClick}
            />
        </div>)
}