import React from 'react'

import searchIcon from '../../static/svg/search.svg'
import ReactSVG from 'react-svg'
import ReactStars from 'react-stars'

import {API_KEY} from "../../utils/constants"

import {SearchSuggestion} from "./search-suggestion"

import {formSearchSuggestionData} from "../../utils/functions";


export const SearchForm = (props) =>{
    const [query, setQuery] = React.useState('')
    const [searchResults, setSearchResults] = React.useState([])

    const typeQuery = (e) => {
        setQuery(e.target.value)
    }
    const selectOption = () => {
        setQuery('')
        searchResults([])
    }
    React.useEffect(()=>{
        if (query.length < 3) return;
        fetch(`https://api.themoviedb.org/3/search/multi?api_key=${API_KEY}&language=en-US&query=${query}&page=1&include_adult=false`)
            .then((response) => {
            if (response.ok) {
                return response.json()
            }
            throw new Error(`Cannot connect to the server to get items `)
        }).then((data) => setSearchResults(data.results))
    }, [query])


    return (
        <form className={(searchResults.length && query.length >= 3 ) ? `search-form search-form_active` : `search-form` }>
            <div className="search-group">
                <input type="search" className='search-input' onChange={typeQuery} value={query} placeholder={'Search..'}/>
                <button type='submit' className='search-btn'><ReactSVG svgClassName='search-btn__icon' src={searchIcon}/></button>
            </div>
            <div className="search-suggestions" >
            { (query.length >= 3 ) &&  searchResults.slice(0,5).map((result) => {

                const {mediaType, year, rating, image, caption, id} = formSearchSuggestionData(result)

                return (
                    <SearchSuggestion key={caption} mediaType = {mediaType} year = {year} rating = {rating}  image = {image} caption={caption} id={id} selectOption={selectOption}/>
                )
            })}
            </div>
        </form>
    )
}