import React from 'react';
import ReactStars from 'react-stars'

import {formMovieCardData} from "../../utils/functions";


//buttons
import {AddToFavoritiesBtn} from "../buttons/add-to-favorities"
import {AddToWishlistBtn} from "../buttons/add-to-wishlist"
import {AddToWatchedBtn} from "../buttons/add-to-watched"
import {PlayTrailerBtn} from "../buttons/play-trailer"


import {Link} from 'react-router-dom';





export const MovieCard = (props) => {

    const data = formMovieCardData(props.data)

    return (<div
        className={props.extraClasses ? "movie-card " + props.extraClasses["movie-card"] : "movie-card"}>
        <div className="marks">
            {data.new && <div className="new">NEW!</div>}
            {data.adult && <div className="adult"><span>18+</span></div>}
        </div>
        <div className="movie-card__actions">
            <AddToFavoritiesBtn data={props.data}  mediaType={data.mediaType}/>
            <AddToWishlistBtn data={props.data}  mediaType={data.mediaType}/>
            <AddToWatchedBtn data={props.data}  mediaType={data.mediaType}/>
            <PlayTrailerBtn id={data.id} mediaType={data.mediaType}/>
        </div>
        <Link className="movie-card__img"
              to={{
                  pathname: `/${data.mediaType}/${data.id}/${data.originalTitle.toLowerCase().replace(/\s+/g, '_')}`,
              }}
        >
            <img src={data.poster} alt={data.shortTitle}/>
        </Link>
        <div className='movie-card__info'>
            <div className="movie-card__title">{data.shortTitle}</div>
            <div className="movie-card__stat">
                <div className={'year'}>{data.year} </div>
                <div className='stars'><ReactStars count={5}
                                                   half={true}
                                                   edit={false}
                                                   size={20}
                                                   value={data.rating / 2}/></div>
            </div>
        </div>

    </div>)
}


