import React from 'react';
import ReactStars from 'react-stars'
import {Link} from 'react-router-dom';

export const SearchSuggestion = (props) => {
    return (
        <Link className='search-suggestion'
              to={{
                  pathname: `/${props.mediaType}/${props.id}/${props.caption.toLowerCase().replace(/\s+/g, '_')}`,
              }}
              onClick={props.selectOption}
        >
            <div className={'search-suggestion__img'}>
                <img src={props.image} alt={props.caption}/>
            </div>
            <div className="search-suggestion__label">
                <div className="title">{props.caption}</div>
                <div className='details'>
                    {props.year && <div className="year">{props.year}</div>}
                    <div className="media-type">{props.mediaType}</div>
                    {props.rating && <div className="stars"><ReactStars count={5} half={true} edit={false}  value={props.rating} size={18}/></div>}
                </div>
            </div>
        </Link>
    )
}
