import React from 'react';

import ReactSVG from 'react-svg'
import {Link} from 'react-router-dom';


import sandwichIcon from '../../static/svg/sandwich.svg'
import listIcon from '../../static/svg/list.svg'
import logoIcon from '../../static/images/logo.png'
import watchedIcon from '../../static/svg/checked.svg'
import wishListIcon from '../../static/svg/bookmark.svg'
import favoritiesIcon from '../../static/svg/like.svg'
import historyIcon from '../../static/svg/history-clock.svg'

import {store} from "../../utils/store"
import {connect} from 'react-redux'


import {SearchForm} from "./SearchForm";

const Navbar_ = (props) => {
    const navbarMenu = React.useRef()
    const [opened, setOpened] = React.useState(false)


    const openNavbarMenu = () =>{
        navbarMenu.current.classList.toggle('navbar-menu__open')
        setOpened(true)
    }
    const closeNavbarMenu = (event) => {
        if (navbarMenu.current && !navbarMenu.current.contains(event.target)) {
            navbarMenu.current.classList.remove('navbar-menu__open')
            setOpened(false)
        }
    }
    React.useEffect(()=>{
        if (opened){
            document.addEventListener('mousedown', closeNavbarMenu);
        }
        return  () => document.removeEventListener('mousedown', closeNavbarMenu);
    }, [opened,
    ])

    return (<nav className={'navbar'}>
        <div className='navbar-menu' ref={navbarMenu}>
            <div className="navbar-menu__sandwich"  onClick={openNavbarMenu}>
                <ReactSVG src={sandwichIcon} svgClassName='sandwich__icon'/>
            </div>
            <div className='dropdown-menu'>
                <Link className='dropdown-menu__item'
                      to={
                          {pathname: `/movies`}
                      }
                >
                    Movies
                </Link>
                <Link className='dropdown-menu__item'
                      to={
                          {pathname: `/tv_shows`}
                      }
                >
                    TV Shows
                </Link>
                <Link to={
                    {pathname: `/genres`}
                }
                      className='dropdown-menu__item'>Genres
                </Link>
                <Link className='dropdown-menu__item'
                      to={
                          {pathname: `/discover`}
                      }
                >
                    Discover
                </Link>
            </div>
        </div>
        <div className={'logo'}>
            <Link to={{pathname: `/`}} >
                <img src={logoIcon} alt={'logo.png'} className={'logo__icon'}/>
            </Link>
        </div>
        <ul className='nav-list'>
            <li className={'nav-list__item'}>
                <Link to={{
                    pathname: `/movies`,
                }}
                >Movies</Link>
            </li>
            <li className='nav-list__item'><Link to={{
                pathname: '/tv_shows'
            }}>TV Shows</Link>
            </li>
            <li className='nav-list__item recommendations'><Link to={{
                pathname: '/recommendations'
            }}>Recommendations</Link>
            </li>
        </ul>
        <SearchForm/>
        <div className='user-lists'>
            <Link to={{pathname: '/lists'}} className='btn user-lists__btn'>
                <ReactSVG src={listIcon} svgClassName='user-lists__icon'/>
                <span>Marked</span>
            </Link>
            <div className='dropdown-menu'>
                <div className='dropdown-menu__item'><ReactSVG src={favoritiesIcon}
                                                               svgClassName={'dropdown-menu__icon'}/><span
                    className='user-lists__link'>Favorities</span> <span
                    className={'count'}>{props.favorities.length}</span></div>
                <div className='dropdown-menu__item'>
                    <ReactSVG src={wishListIcon} svgClassName={'dropdown-menu__icon'}/><span
                    className='user-lists__link'>Wishlist</span> <span
                    className={'count'}>{props.wishlist.length}</span>
                </div>
                <div className='dropdown-menu__item'>
                    <ReactSVG src={watchedIcon} svgClassName={'dropdown-menu__icon'}/><span
                    className='user-lists__link'>Watched</span><span className={'count'}>{props.watched.length}</span>
                </div>
            </div>
        </div>
    </nav>)
}

const mapStateToProps = (state) =>{
    return state.lists
}
export const Navbar = connect(mapStateToProps)(Navbar_)

