import React from 'react'


import ReactSVG from 'react-svg'
import wishListIcon from '../../static/svg/bookmark.svg'
import {store} from "../../utils/store";
import {ActionTypes} from "../../utils/constants";
import {AddToFavoritiesBtn_} from "./add-to-favorities";
import {connect} from "react-redux";


const AddToWishlistBtn_ = (props) => {

    const data = {...props.data, mediaType: props.mediaType}

    const isActive = (data) => {
        return props.wishlist.some((item) => {
            return item.id === data.id
        })
    }
    const active = isActive(data)


    const addToWishlist = (data) => {
        if (active) {
            store.dispatch({
                type: ActionTypes.REMOVE_FROM_WISHLIST,
                item: data
            })

            return;
        }
        store.dispatch({type: ActionTypes.ADD_TO_WISHLIST, item: data})
    }

    return (
        <div className={(active) ? "add-to-wishlist add-to-list_active" : 'add-to-wishlist'}
             onClick={() => addToWishlist(data)}>
            <ReactSVG src={wishListIcon}
                      svgClassName='action-icon add-to-wishlist__icon'/>
        </div>)
}


const mapStateToProps = (state)=>{
    return state.lists
}

export const AddToWishlistBtn = connect(mapStateToProps)(AddToWishlistBtn_)