import React from 'react';

import ReactSVG from 'react-svg'
import arrowIcon from '../../static/svg/arrow.svg'




export const Controls = (props) =>{
    return(
        <div className={`controls ${props.extraClasses}`}>
            <div ref={props.controlPrev}
                 className={(props.controlPrevBlocked) ? 'control control-prev control__blocked' : 'control control-prev' }
                 onClick={props.prev}>
                <ReactSVG
                src={arrowIcon}
                svgClassName='left control__icon'/>
            </div>

            <div ref={props.controlNext}
                 className={(props.controlNextBlocked) ? 'control control-next control__blocked' : 'control control-next' }
                 onClick={props.next}>
                <ReactSVG
                    src={arrowIcon}
                    svgClassName='right control__icon'/>
            </div>
        </div>
    )
}