import React from 'react';



export const DropdownSortBy = (props) => {
    const [active, setActive] = React.useState(false)

    const dropdown = React.useRef()

    const toggleDropdown = (e) =>{
        dropdown.current.classList.toggle('dropdown_active')
        setActive(true)
    }
    const closeDropdown = (e) => {
        if (dropdown.current && !dropdown.current.contains(e.target)) {
            setActive(false)
        }
    }

    const selectOption = (option)=> {
        return () => {
            props.goToFirstPage()
            setActive(false)
            props.dispatch({sortBy: option})
        }
    }


    React.useEffect(()=>{
        if (active){
            document.addEventListener('mousedown', closeDropdown);
        }
        return  () => document.removeEventListener('mousedown', closeDropdown);
    }, [active])


    return (
        <div className={(active) ? 'dropdown dropdown_active' : 'dropdown'} ref={dropdown}>
            <div className='choosed-values dropdown__value' onClick={toggleDropdown}>
                {props.sortBy.name}
            </div>
            <ul  className='dropdown-select'>
                {props.sortByOptions.map((option)=>{
                    const className = (props.sortBy.name ===  option.name ) ? 'dropdown-select__option dropdown-select__option_active' : 'dropdown-select__option'
                    return (<li className={className}
                                key={option.name}
                                onClick={selectOption(option)}>
                        {option.name}</li>)
                })}
            </ul>
        </div>
    )
}