import React from 'react'
import {API_KEY, movieGenres} from "../../utils/constants"


import closeIcon from '../../static/svg/close-cross.svg'
import ReactSVG from 'react-svg'



export const DropdownGenres = (props) => {

    const [keywordsSuggestions, setKeywords] = React.useState([])
    const [query , setQuery] = React.useState('')
    const [active, setActive] = React.useState(false)
    const dropdown  = React.useRef()
    const input = React.useRef()
    const handleInput = (e) => {
        setQuery(e.target.value)
    }
    const selectOption = (suggestion) =>{
        props.goToFirstPage()
        setActive(false)
        props.dispatch({genres: [...props.genres, suggestion]})
        setQuery('')
    }

    const deleteGenre = (genre) => {
        const genres = props.genres.slice().filter(elem => elem.id !== genre.id)
        props.dispatch({genres})
    }

    const closeDropdown = (e) => {
        if (dropdown.current && !dropdown.current.contains(e.target) && !input.current.value ) {
            setActive(false)
        }
    }

    React.useEffect(() => {
        document.addEventListener('mousedown', closeDropdown);
        return () => document.removeEventListener('mousedown', closeDropdown);
    }, [query])


    const suggestions = (query) ? props.suggestions.filter((genre) => {
        return (genre.name.indexOf(query) !== -1)
    }) : props.suggestions

    return (
        <div className={(active) ? 'dropdown dropdown_active' : 'dropdown'} ref={dropdown}>
            <div className='input-group'>
                <ul className="input-group__added-values">
                    {props.genres.map((genre) => {
                        return (
                            <li className="input-group__added-value" key={genre.id}>
                                <span>{genre.name}</span>
                                <span className='close' onClick={() => deleteGenre(genre)}>
                                    <ReactSVG svgClassName='close__icon' src={closeIcon}/>
                                </span>
                            </li>
                        )
                    })}

                </ul>
                <input type="text" className='input-group__input' placeholder='Filter by genres...'
                       onChange={handleInput} onSelect={() => setActive(true)} value={query} ref={input}/>
            </div>
            <ul className='dropdown-select' style={(suggestions.length <= 4) ? {height: 'auto'} : {}}>
                {suggestions
                    .filter((suggestion) => {
                        return (!props.genres.some((genre) => suggestion.id === genre.id))
                    })
                    .map((genre) => {
                        return (
                            <li className='dropdown-select__option' key={genre.id}
                                onClick={() => selectOption(genre)}>{genre.name}</li>
                        )
                })}
            </ul>
        </div>
    )
}