import React from 'react';
import ReactSVG from 'react-svg'

import GenresSlider from "react-slick";

import arrowIcon from '../../static/svg/arrow.svg'

import {movieGenres} from "../../utils/constants"
import {Genre} from "../genre";


const NextArrow = (props) =>(
    <div className={'genre-arrow arrow-next '} onClick={props.onClick}>
        <ReactSVG src={arrowIcon} svgClassName='genre-arrow__icon'/>
    </div>
)
const PrevArrow = (props) =>(<div className={' genre-arrow arrow-prev'}  onClick={props.onClick}>
        <ReactSVG src={arrowIcon} svgClassName='genre-arrow__icon'/>
    </div>)

export const GenresBlock = (props) =>{

    const settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 6,
        slidesToScroll: 6,
        draggable: true,
        prevArrow: <PrevArrow/>,
        nextArrow: <NextArrow/>,
    }

    return (
        <div className='genres-block' >
            <h1>{props.name}</h1>
            <GenresSlider {...settings} >
                {props.genres.map((genre) => {
                    return (
                        <Genre
                            mediaType={props.mediaType}
                            key={genre.id}
                            genre={genre}

                        />
                    )
                })}
            </GenresSlider>
        </div>)
}