import {createStore, combineReducers} from 'redux'

import {ActionTypes} from "./constants";
import {removeItemFromList} from "./functions";


import { persistStore, persistReducer } from 'redux-persist'
import storage from 'redux-persist/lib/storage' // defaults to localStorage for web

const initialListsState = {
    favorities: [],
    wishlist: [],
    watched: [],
}




const initialTrailersModalState = {
    showModal: false,
    trailersData: {},
    currentVideoKey: 0,
}
const trailersModalReducer = (state = initialTrailersModalState , action ) =>{
    switch (action.type){
        case ActionTypes.SHOW_MODAL:
            return {...state, showModal: true}
        case ActionTypes.HIDE_MODAL:
            return {...initialTrailersModalState}
        case ActionTypes.SET_VIDEO_KEY:
            return {...state, currentVideoKey: action.currentVideoKey}
        case  ActionTypes.SET_TRAILERS_DATA:
            return {...state, trailersData: action.trailersData}

        default:
            return state

    }
}

const listsReducer = (state = initialListsState , action)=>{
    switch (action.type){
        case ActionTypes.ADD_TO_FAVORITIES:
            return {...state, favorities: [...state.favorities, action.item]}
        case ActionTypes.REMOVE_FROM_FAVORITIES:
            const favorities = removeItemFromList(state.favorities, action.item)
            return {...state, favorities}

        case ActionTypes.ADD_TO_WISHLIST:
            return {...state, wishlist: [...state.wishlist, action.item]}
        case ActionTypes.REMOVE_FROM_WISHLIST:
            const wishlist = removeItemFromList(state.wishlist, action.item)
            return {...state, wishlist }

        case ActionTypes.ADD_TO_WATCHED:
            return {...state, watched: [...state.watched, action.item]}
        case ActionTypes.REMOVE_FROM_WATCHED:
            const watched = removeItemFromList(state.watched, action.item)
            return {...state, watched }

        default:
            return state
    }
}

const persistConfig = {
    key: 'root',
    storage,
}

const rootReducer = combineReducers({
    lists: listsReducer,
    trailersModal: trailersModalReducer,
});
const persistedReducer = persistReducer(persistConfig, rootReducer)



export const store = createStore(
    persistedReducer,
    window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__(),
)

export const persistor = persistStore(store)





