import React from 'react';



export const YearSettings = (props) => {

    const handleOptionChange = (e) => {
        props.handleOptionChange(e)
    }

    return(
        <div  className="year-settings">
            <div className={(props.radioState.selectedOption === 'range') ? "year-setting year-setting_active" : 'year-setting'}>
                <label htmlFor="range-year" className="year-setting__label">
                    range
                </label>
                <div className="year-setting__radio">
                    <input type="radio"
                           name='year'
                           value='range'
                           id='range-year'
                           checked={props.radioState.selectedOption === 'range'}
                           onChange={handleOptionChange}
                    />
                </div>
            </div>
            <div  className={(props.radioState.selectedOption === 'single') ? "year-setting year-setting_active" : 'year-setting'}>
                <label htmlFor='single-year'  className="year-setting__label">
                    single
                </label>
                <div className="year-setting__radio">
                    <input type="radio"
                           value="single"
                           name='year' id='single-year'
                           checked={props.radioState.selectedOption === 'sinlge'}
                           onChange={handleOptionChange}
                    />
                </div>
            </div>
        </div>
    )
}