import React from 'react';
import ReactSVG from 'react-svg'


//components
import {Header} from "../../components/header"
import {Banner} from "../../components/banner"
import {Container} from "../../components/container"
import {CastSlider} from "../../components/cast-slider"
import {SimilarMovies} from "../../components/similar-movies"
import {GenresBlock} from "../../components/block-genres"
import {Footer} from "../../components/footer"

//icons
import imdbLogoIcon from '../../static/images/imdb-icon.png'
import tmdbLogoIcon from '../../static/svg/tmdb-logo.svg'



//CONST-S
import {API_KEY, movieGenres, tvGenres} from "../../utils/constants"
import {API_KEY_OMDB} from "../../utils/constants"

//helpers
import {formTvStateData} from "../../utils/functions";

//buttons
import {AddToFavoritiesBtn} from "../../components/buttons/add-to-favorities"
import {AddToWishlistBtn} from "../../components/buttons/add-to-wishlist"
import {AddToWatchedBtn} from "../../components/buttons/add-to-watched"
import {PlayTrailerBtn} from "../../components/buttons/play-trailer"



export const TVShow = (props) => {

    const movie_id = window.location.pathname.match( /\d+/ )[0]

    const reducer = (prevState, newState) => {
        return {...prevState, ...newState}
    }

    const [moviePageState, setMoviePageState] = React.useReducer(reducer , {
        credits: {
            cast: [],
            crew: []
        },
        details: [],
        imdbScore: undefined,
        similarMovies: []
    })






    React.useEffect(()=>{
        // details
        fetch(`https://api.themoviedb.org/3/tv/${movie_id}?api_key=${API_KEY}&language=en-US`)
            .then((response) => {
                if (response.ok){
                    return response.json()
                }
                throw new Error(`Cannot connect to the server to get items `)
            })
            .then((data)=> {
                setMoviePageState({details: data})
                return data.imdb_id
            })
            .then(fetch(`https://api.themoviedb.org/3/tv/${movie_id}/external_ids?api_key=${API_KEY}&language=en-US`)
                .then((response) => {
                    if (response.ok){
                        return response.json()
                    }
                    throw new Error(`Cannot connect to the server to get items `)
                    // imdb score
                }).then(({imdb_id}) => {
                    fetch(`http://www.omdbapi.com/?i=${imdb_id}&apikey=${API_KEY_OMDB}`)
                        .then((response) => {
                            if (response.ok){
                                return response.json()
                            }
                            throw new Error(`Cannot connect to the server to get items `)})
                        .then((data)=> {
                            setMoviePageState({imdbScore : data.imdbRating})
                            window.scrollTo({top: 0})
                        })
                })
            )

        // similar movies
        fetch(`https://api.themoviedb.org/3/tv/${movie_id}/similar?api_key=${API_KEY}&language=en-US&page=1`)
            .then((response) => {
                if (response.ok){
                    return response.json()
                }
                throw new Error(`Cannot connect to the server to get items `)})
            .then((data)=> {
                setMoviePageState({similarMovies: data.results})
            })


        //credits
        fetch(`https://api.themoviedb.org/3/tv/${movie_id}/credits?api_key=${API_KEY}&language=en-US`)
            .then((response) => {
                if (response.ok){
                    return response.json()
                }
                throw new Error(`Cannot connect to the server to get items `)
            }).then((data)=> {
            const {crew, cast} = data
            setMoviePageState({credits: {crew, cast}})
        })

    },[movie_id])



    const {credits, details, imdbScore, similarMovies} = moviePageState



    const {
        backdrop_path,
        genres,
        original_language,
        id,
        production_companies,
        title,
        poster_path,
        tmdbScore,
        country,
        year,
        overview,
        seasons
    } = formTvStateData(details, credits)



    return ( id ) ?  (
        <React.Fragment>
            <Header/>
            {backdrop_path && <Banner title={title}
                                      backdrop_path={backdrop_path}
                                      extraClasses={'banner_fixed'}/>}
            <Container>
                <div className='movie-page'>
                    <div className="movie-inner">
                        <div className="movie-inner__col-1">
                            <div className="movie-thumbnail" style={{backgroundImage: `url(${poster_path})`}}>
                            </div>
                            <div className="rating-block">
                                { imdbScore !== 'N/A' &&
                                <div className="rating imdb-rating">
                                    <div className="rating__count">
                                        {imdbScore}
                                    </div>
                                    <div className='rating__info'>
                                        <div className='rating__by'>
                                            <img src={imdbLogoIcon} alt="imdb-logo.png"/>
                                        </div>
                                    </div>
                                </div>}
                                {(tmdbScore > 0) &&
                                <div className="rating">
                                    <div className="rating__count">
                                        {tmdbScore}
                                    </div>
                                    <div className='rating__info'>
                                        <div className='rating__by'>
                                            <ReactSVG src={tmdbLogoIcon} svgClassName='tmdb-logo'/>
                                        </div>
                                    </div>
                                </div>}
                            </div>
                            <div className="movie-actions">
                                <AddToFavoritiesBtn data={moviePageState.details} mediaType={'tv'} />
                                <AddToWishlistBtn data={moviePageState.details} mediaType={'tv'}/>
                                <AddToWatchedBtn data={moviePageState.details} mediaType={'tv'}/>
                                <PlayTrailerBtn id={id} mediaType={'tv'} />
                            </div>
                        </div>
                        <div className="movie-inner__col-2">
                            <div className='info-block'>
                                <ul className='basic-info'>
                                    <li className="info-block__item  year">{year}</li>
                                    {country &&
                                    <li className="info-block__item country">{country}</li>}
                                    {original_language &&
                                    <li className="info-block__item lang">{original_language}</li>}
                                </ul>
                                <ul className='genres-info'>
                                    {genres.slice(0,3).map((genre)=> {
                                        return (
                                            <li key={genre.name} className="info-block__item genres-info__item">{genre.name}</li>
                                        )
                                    })}
                                </ul>
                            </div>
                            <div className="title-block">
                                <div className="title"><h1>{title}</h1></div>
                            </div>
                            <div className="overview">
                                {overview}
                            </div>
                            <div className='movie-details'>
                                <div className="cast"><span>Cast:</span>{credits['cast'].slice(0,8).map((member, i) => {
                                    if (i === 7){
                                        return ` ${member['name']}`
                                    }
                                    return ` ${member['name']}, `
                                })} </div>
                                { production_companies.length > 0 && <div><span>Production Co:</span>{production_companies
                                    .map((company,i) => ( i === 0 ) ? company.name : `, ${company.name}`)}</div>}
                                <div className='sesons'><span>seasons:</span>{seasons.length}</div>
                            </div>
                            {(credits.cast.length) ?
                            <div className='cast'>
                                <div className='cast-title'>Cast</div>
                                <CastSlider cast={credits.cast}/>
                            </div> : null}
                        </div>
                    </div>
                </div>
                {similarMovies.length > 0 && <SimilarMovies id={id}
                                                            name={'Similar TV Shows'}
                                                            similarMovies={similarMovies}/>}
                <GenresBlock
                    mediaType={'tv'}
                    genres={tvGenres}
                    name={'TV Genres'}
                />
            </Container>
            <Footer/>
        </React.Fragment>
    ) : null
}
