import React from 'react'


import {store} from "../../utils/store"

import ReactSVG from 'react-svg'
import playIcon from '../../static/svg/play-button.svg'
import {API_KEY} from "../../utils/constants";

import {ActionTypes} from "../../utils/constants";


export const PlayTrailerBtn = (props) =>{

    const playTrailers = () => {

        const query = `https://api.themoviedb.org/3/${props.mediaType}/${props.id}/videos?api_key=${API_KEY}&language=en-US`

        fetch(query)
            .then((response) => {
                if (response.ok) {
                    return response.json()
                }
                throw new Error(`Cannot connect to the server to get items `)

            }).then((data) => data.results).then((data) => {
                if (!data.length) return;
            store.dispatch({type: ActionTypes.SET_VIDEO_KEY, currentVideoKey: data[0].key})

            const mutatedData = {}

            data.forEach((video) => {
                if (video.site === "YouTube") {
                    if (mutatedData[video.type]) {
                        mutatedData[video.type].push(video.key)
                    } else {
                        mutatedData[video.type] = []
                        mutatedData[video.type].push(video.key)
                    }
                }
            })
            store.dispatch({type: ActionTypes.SET_TRAILERS_DATA, trailersData: mutatedData})
        }).then(store.dispatch({type: ActionTypes.SHOW_MODAL}))
    }

    return (
        <div className={"play-trailer"}
             onClick={playTrailers}>
            <ReactSVG src={playIcon} svgClassName='action-icon play-trailer__icon'/>
        </div>
    )
}
